.card__button-like {
  width: 22px;
  height: 19px;
  background-image: url('../../../images/like.svg');
  background-color: #fff;
  border: 0;
  margin: 0;
  padding-right: 2px;
  cursor: pointer;
  transition: opacity 0.5s linear 0.3s;
}

.card__button-like:hover {
  opacity: 50%;
}

